module.exports = {

	//---------------  ---------------//
	emitLogin: () => {
		uni.$emit('loginEvent')
	},
	onLogin: (func) => {
		uni.$on('loginEvent', func)
	},
	offLogin: () => {
		uni.$off('loginEvent')
	},

	//---------------  ---------------//
	emitCollect: () => {
		uni.$emit('collectEvent')
	},
	onCollect: (func) => {
		uni.$on('collectEvent', func)
	},
	offCollect: () => {
		uni.$off('collectEvent')
	}

}
