const checkRegister = function(account, pwd, repwd) {
	let msg
	if (utils.isEmpty(account)) {
		msg = ''
	} else if (utils.isEmpty(pwd)) {
		msg = ''
	} else if (utils.isEmpty(repwd)) {
		msg = ''
	} else if (pwd != repwd) {
		msg = ''
	}
	utils.toast(msg)
	return msg == undefined
}

